@font-face {
  font-family: "Open Sans";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  height: 100%;
  font-size: 15px;
  font-family: "Open Sans", "Arial", sans-serif;
  line-height: 24px;
}
.page__body {
  min-height: 100%;
  margin: 0;
  color: #000000;
  word-wrap: break-word;
}
.title {
  font-style: normal;
  font-weight: bold;
  font-size: 25px;
  line-height: 30px;
  margin: 0;
}
@media (min-width: 768px) {
  .title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .title {
    font-size: 53px;
    line-height: 57px;
  }
}
.text {
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 24px;
  margin: 0;
}
@media (min-width: 768px) {
  .text {
    font-size: 17px;
    line-height: 30px;
  }
}
.button {
  display: block;
  font-weight: bold;
  font-size: 18px;
  line-height: 16px;
  text-align: center;
  text-transform: uppercase;
  color: #444444;
  background-color: #ffffff;
  padding: 16px 0;
  border: 2px solid #444444;
  text-decoration: none;
  cursor: pointer;
}
.button:hover {
  background-color: #444444;
  color: #ffffff;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .button {
    font-size: 17px;
  }
}
.field {
  margin: 0;
  padding: 17px 0;
  font-family: inherit;
  font-size: 17px;
  line-height: 18px;
  width: 100%;
  background-color: transparent;
  border: none;
  border-bottom: 2px solid #000000;
  outline: none;
  box-sizing: border-box;
}
.field::placeholder {
  color: #00000030;
}
.field:hover {
  border-bottom: 2px solid #62d1ba;
}
.field:focus {
  color: #444444;
  border-color: #62d1ba;
}
@media (min-width: 768px) {
  .field {
    padding: 12px 17px;
    font-size: 19px;
  }
}
.page-header__logo:hover {
  opacity: 0.6;
}
.page-header__logo:active {
  opacity: 0.3;
}
.page-header__logo_desktop {
  display: none;
}
@media (min-width: 768px) {
  .page-header__logo img {
    min-width: 113px;
    min-height: 46px;
  }
}
@media (min-width: 1150px) {
  .page-header__logo img {
    min-width: 149px;
    min-height: 72px;
  }
  .page-header__logo_desktop {
    display: block;
    padding-right: 30px;
  }
}
.main-nav {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: auto;
}
.logo-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 30px;
  position: relative;
  width: auto;
  min-height: 79px;
  border-bottom: 1px solid #ececec;
}
.main-nav__toggle {
  position: absolute;
  top: 32px;
  right: 30px;
  width: 20px;
  height: 14px;
  background-color: #ffffff;
  cursor: pointer;
  border: none;
}
.main-nav_closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 6px 0 0 #000000, 0 12px 0 0 #000000;
}
.main-nav_closed .main-nav__toggle:active::before {
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow: 0 6px 0 0 rgba(0, 0, 0, 0.3), 0 12px 0 0 rgba(0, 0, 0, 0.3);
}
.main-nav_opened .main-nav__toggle {
  height: 21px;
}
.main-nav_opened .main-nav__toggle::before,
.main-nav_opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 7px;
  right: 0;
  width: 21px;
  height: 2px;
  background-color: #000000;
}
.main-nav_opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav_opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav_opened .main-nav__toggle:active::before,
.main-nav_opened .main-nav__toggle:active::after {
  background-color: rgba(0, 0, 0, 0.3);
}
.main-nav__wrapper {
  display: flex;
  flex-direction: column;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.site-list__item,
.user-list__item {
  border-bottom: 1px solid #ececec;
  padding: 0 30px;
  min-width: 0;
}
.main-nav__link {
  display: block;
  padding: 28.5px 0 26.5px 46px;
  font-style: normal;
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  text-decoration: none;
}
.main-nav__link:hover {
  color: rgba(0, 0, 0, 0.6);
}
.main-nav__link:active {
  color: rgba(0, 0, 0, 0.3);
}
@media (max-width: 767px) {
  .main-nav_closed .main-nav__wrapper {
    display: none;
  }
  .main-nav_nojs .main-nav__toggle {
    display: none;
  }
  .main-nav_nojs .main-nav__wrapper {
    display: flex;
  }
}
@media (min-width: 768px) {
  .main-nav {
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0 50px;
    max-width: 1050px;
  }
  .site-list {
    display: none;
  }
  .logo-wrapper {
    border: none;
    padding: 0;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav__link {
    padding: 32.5px 0 31.5px;
  }
  .main-nav__link-text {
    display: none;
  }
}
@media (min-width: 1150px) {
  .main-nav {
    display: block;
  }
  .logo-wrapper {
    display: none;
  }
  .main-nav__wrapper {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .main-nav__link-text {
    display: none;
  }
  .site-list {
    display: flex;
    flex-wrap: wrap;
    max-width: 450px;
  }
  .site-list__item {
    padding: 0;
    padding-right: 35px;
    margin-right: 35px;
    border-bottom: none;
    border-right: 1px solid #ececec;
    max-width: 185px;
  }
  .site-list__item:last-child {
    margin: 0;
    padding: 0;
    border: none;
  }
}
.user-list__link {
  position: relative;
}
.user-list__link:hover .user-list__icon-search,
.user-list__link:hover .user-list__icon-cart {
  fill: #63d1bb;
  opacity: 1;
}
.user-list__link:active .user-list__icon-search,
.user-list__link:active .user-list__icon-cart {
  fill: #63d1bb;
  opacity: 0.3;
}
.user-list__icon-search {
  position: absolute;
  top: 32px;
  left: 0;
  width: 18px;
  height: 18px;
  fill: #000000;
  opacity: 0.2;
}
.user-list__icon-cart {
  position: absolute;
  top: 32px;
  left: 0;
  width: 21px;
  height: 18px;
  fill: #000000;
  opacity: 0.2;
}
@media (min-width: 768px) {
  .user-list {
    display: flex;
    flex-wrap: wrap;
    max-width: 400px;
  }
  .user-list__item {
    padding: 0;
    border-left: 1px solid #ececec;
    border-bottom: none;
    max-width: 264px;
  }
  .user-list__link_icon_search {
    padding: 0;
    min-width: 82px;
    min-height: 88px;
  }
  .user-list__link_icon_cart {
    padding-left: 82px;
  }
  .user-list__icon-search {
    left: 31px;
  }
  .user-list__icon-cart {
    left: 31px;
  }
}
.sub-nav {
  background-color: #f9f9f9;
}
.sub-nav_color_grey {
  background-color: #f1f1f1;
}
.sub-nav__list {
  display: none;
  margin: 0;
  padding: 0;
  list-style: none;
}
.sub-nav__link {
  display: block;
  font-weight: bold;
  padding: 21px 0 21px;
  color: #000000;
  text-decoration: none;
}
.sub-nav__link:hover {
  color: rgba(0, 0, 0, 0.6);
}
.sub-nav__link:active {
  color: rgba(0, 0, 0, 0.3);
}
.sub-nav__signature {
  padding-top: 19.5px;
  display: none;
}
@media (min-width: 768px) {
  .sub-nav {
    border-top: 1px solid #ececec;
  }
  .sub-nav__wrapper {
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    padding: 0 50px;
    max-width: 1050px;
  }
  .sub-nav__item {
    margin-right: 37px;
    max-width: 174px;
    min-width: 0;
    word-wrap: break-word;
  }
  .sub-nav__item:last-child {
    margin-right: 0;
  }
  .sub-nav__list_tablet {
    display: flex;
  }
  .sub-nav__signature {
    display: block;
    min-width: 231px;
    max-width: 234px;
    word-wrap: break-word;
  }
}
@media (min-width: 1150px) {
  .sub-nav__list {
    display: flex;
    flex-wrap: wrap;
  }
  .sub-nav__list_tablet {
    display: none;
  }
  .sub-nav__item {
    margin-right: 61px;
  }
}
.categories {
  position: relative;
  background-image: url("../img/hero-mobile.jpg");
  background-repeat: no-repeat;
  background-position: 50% -18px;
  background-color: #f9f9f9;
}
@media (min-resolution: 2dppx) {
  .categories {
    background-image: url("../img/hero-mobile@2x.jpg");
    background-size: 320px auto;
  }
}
.categories__wrapper {
  display: flex;
  flex-direction: column;
  width: auto;
  min-height: 489px;
  margin: 0 auto;
}
.categories__title {
  max-width: 260px;
  text-align: center;
  margin: 73px auto 0;
  padding: 0 30px 30px;
  font-size: 27px;
}
.categories__list {
  display: flex;
  flex-direction: column;
  margin: auto 0 0;
  padding: 0;
  list-style: none;
}
.categories__link {
  display: block;
  position: relative;
  padding: 24px 37px 31px 30px;
  background-color: #63d1bb;
  color: #ffffff;
  font-weight: bold;
  font-size: 19px;
  line-height: 24px;
  text-decoration: none;
}
.categories__link:hover {
  transform: translateY(-5px);
}
.categories__link_interior::after {
  position: absolute;
  content: url("../img/interior.svg");
  top: 23px;
  right: 36px;
  opacity: 0.5;
}
.categories__link_toys {
  background-color: #56c3b2;
}
.categories__link_toys::after {
  position: absolute;
  content: url("../img/toy-icon.svg");
  top: 23px;
  right: 36px;
  opacity: 0.5;
}
.categories__link_interior:hover::after,
.categories__link_toys:hover::after {
  opacity: 1;
}
.categories__link_interior:active::after,
.categories__link_toys:active::after {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .categories {
    position: relative;
    background-image: url("../img/hero-desktop.jpg");
    background-repeat: no-repeat;
    background-position: 50% -36px;
  }
  .categories::after {
    position: absolute;
    content: "";
    bottom: -12px;
    height: 24px;
    width: 100%;
    background-image: url("../img/pattern-bg/zigzag-fill-tablet.svg");
    background-position: bottom;
    background-repeat: repeat-x;
  }
  .categories__wrapper {
    max-width: 1050px;
    padding: 0 50px;
    min-height: 679px;
  }
  .categories__title {
    max-width: 668px;
    margin-top: 88px;
    font-size: 43px;
  }
  .categories__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-end;
  }
  .categories__item {
    width: 50%;
  }
  .categories__link {
    padding: 25px 100px 34px 36px;
    font-size: 20px;
    line-height: 30px;
  }
  .categories__link_interior::after,
  .categories__link_toys::after {
    top: 30px;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .categories {
    background-image: url("../img/hero-desktop@2x.jpg");
    background-size: 1150px auto;
    background-position: 50% -36px;
  }
}
@media (min-width: 1150px) {
  .categories {
    background-position: 50% -35px;
    background-size: contain;
  }
  .categories::after {
    height: 44px;
    background-image: url("../img/pattern-bg/zigzag-fill-desktop.svg");
  }
  .categories__wrapper {
    min-height: 679px;
  }
  .categories__title {
    max-width: 700px;
    font-size: 53px;
  }
}
.popular-product {
  position: relative;
}
.popular-product::after {
  position: absolute;
  content: "";
  bottom: 0;
  height: 24px;
  width: 100%;
  background-image: url("../img/pattern-bg/zigzag-line-mobile.svg");
  background-position: bottom;
  background-repeat: repeat-x;
}
.popular-product__wrapper {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 31px 30px 42px;
  width: auto;
}
.popular-product__title {
  margin-bottom: 3.5px;
}
.popular-product__text {
  margin-bottom: 20px;
}
.popular-product__subtitle {
  font-weight: bold;
  font-size: 17px;
  line-height: 20px;
  text-transform: uppercase;
  color: #63d1bb;
  margin: 0 0 17.5px;
}
.popular-product__specifications {
  font-weight: normal;
  font-size: 15px;
  line-height: 20px;
  margin: 0 0 28px;
  padding: 0;
  list-style: none;
}
.popular-product__item {
  display: flex;
  flex-wrap: wrap;
  padding: 7px 0 12px;
  border-bottom: 1px solid #ececec;
}
.popular-product__property {
  width: 97px;
}
.popular-product__value {
  min-width: 162px;
}
.popular-product__image-wrapper {
  margin: 0;
}
.popular-product__img {
  width: 100%;
  margin-bottom: 19.5px;
}
.popular-product__price {
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
  color: #444444;
  text-align: center;
  margin-bottom: 24.5px;
}
@media (min-width: 768px) {
  .popular-product::after {
    background-image: url("../img/pattern-bg/zigzag-line-tablet.svg");
  }
  .popular-product__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    align-items: flex-start;
    padding: 39.5px 50px 69px;
    max-width: 1050px;
  }
  .popular-product__title {
    grid-column: 1 / 4;
    margin-bottom: 0;
    padding: 12px 0 37.5px;
  }
  .popular-product__subtitle {
    grid-column: 4 / 5;
    text-align: center;
    line-height: 17px;
    margin: 0;
    padding: 18px 0 0 40px;
  }
  .popular-product__text {
    grid-column: 1 / 3;
    margin: 0;
    padding: 0 42px 33px 0;
  }
  .popular-product__specifications {
    grid-column: 1 / 3;
    margin: 0;
    padding: 0 42px 47px 0;
  }
  .popular-product__image-wrapper {
    margin: 0;
    grid-column: 3 / 5;
    grid-row: 2 / 6;
    padding-top: 13px;
  }
  .popular-product__img {
    display: block;
    object-fit: contain;
    margin: 0;
  }
  .popular-product__price {
    grid-column: 1 / 3;
    font-size: 20px;
    margin: 0;
    padding: 0 42px 0 0;
  }
  .popular-product__button {
    grid-column: 1 / 3;
    margin: 10px 42px 0 0;
    align-self: flex-end;
  }
}
@media (min-width: 1150px) {
  .popular-product::after {
    background-image: none;
  }
  .popular-product__wrapper {
    position: relative;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    padding: 54px 50px 96px;
  }
  .popular-product__wrapper::after {
    position: absolute;
    content: "";
    bottom: 0;
    left: 50px;
    height: 23px;
    width: 1050px;
    background-image: url("../img/pattern-bg/zigzag-line-desktop.svg");
    background-position: bottom;
    background-repeat: repeat-x;
  }
  .popular-product__title {
    grid-column: 2 / 6;
    grid-row: 1 / 2;
    align-self: flex-end;
    padding: 33px 0 36px;
  }
  .popular-product__subtitle {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
    padding: 18px 52px 52px 42px;
    align-self: flex-end;
  }
  .popular-product__text {
    grid-column: 2 / 4;
    padding: 0 58px 0 0;
  }
  .popular-product__specifications {
    grid-column: 2 / 4;
    padding: 30px 58px 0 0;
    transform: translateY(-18px);
  }
  .popular-product__image-wrapper {
    grid-column: 4 / 7;
    grid-row: 1 / 6;
    z-index: -1;
    padding-top: 15px;
  }
  .popular-product__price {
    grid-column: 2 / 4;
    align-self: flex-end;
    padding: 0 58px 0 0;
    transform: translateY(10px);
  }
  .popular-product__button {
    grid-column: 2 / 4;
    margin: 0 58px 0 0;
  }
}
