  content: "";
  bottom: 0;
  height: 24px;
  width: 100%;
  background-image: url("../img/pattern-bg/zigzag-line-mobile.svg");
  background-position: bottom;
  background-repeat: repeat-x;
}
.reviews__wrapper {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 0 30px 51px;
  width: auto;
}
.reviews__title {
  margin-bottom: 14px;
}
.reviews__item {
  margin: 0 0 30px;
}
.reviews__text {
  margin-bottom: 33px;
  line-height: 30px;
}
.reviews__user-name {
  font-style: normal;
  font-weight: bold;
  font-size: 17px;
  line-height: 30px;
}
.reviews__user-nickname {
  display: block;
  font-size: 17px;
  line-height: 30px;
}
.reviews__slider-controls {
  display: flex;
  margin-bottom: 38px;
}
.reviews__slider-nav {
  width: 100%;
