.control {
  display: block;
  padding-left: 60px;
  min-width: 0;
}
.control__mark {
  position: absolute;
  top: 0;
  left: 0;
  width: 38px;
  height: 38px;
  border: 2px solid #444444;
  box-sizing: border-box;
}
.control__input:hover + .control__mark {
  border-color: #62d1ba;
}
.control__input:active + .control__mark {
  border-color: #44444430;
}
.control__input:focus + .control__mark {
  border-color: #56c3b2;
}
.control__label {
  display: block;
  font-weight: bold;
  font-size: 17px;
  line-height: 18px;
}
.control__input[type="radio"] + .control__mark {
  border-radius: 50%;
  width: 42px;
  height: 42px;
}
.control__input[type="radio"]:checked + .control__mark::before {
  position: absolute;
  top: 10px;
  left: 10px;
  width: 18px;
  height: 18px;
  background-color: #62d1ba;
  border-radius: 50%;
  content: "";
}
.control__input[type="checkbox"]:checked + .control__mark {
  background-image: url("../img/tick.svg");
  background-repeat: no-repeat;
  background-position: center;
}
@media (min-width: 768px) {
  .control {
    padding-left: 82px;
  }
  .control__label {
    font-size: 19px;
  }
  .control__mark,
  .control__input[type="radio"] + .control__mark {
    width: 52px;
    height: 52px;
  }
  .control__input[type="radio"]:checked + .control__mark::before {
    top: 13px;
    left: 13px;
    width: 22.7px;
    height: 22.7px;
  }
  .control__input[type="checkbox"]:checked + .control__mark {
    background-size: 31px 22px;
  }
}
.modal-container {
  display: flex;
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #ffffff90;
}
.modal-container_close {
  display: none;
}
.modal {
  flex-grow: 1;
  margin: auto;
  padding: 28.5px 30px 46px;
  width: auto;
  border: 1px solid #c6c6c6;
  background-color: #ffffff;
  box-shadow: 0 5px 10px rgba(0, 1, 1, 0.25);
  box-sizing: border-box;
}
.modal__title {
  font-weight: bold;
  font-size: 25px;
  line-height: 30px;
  text-align: center;
  text-transform: uppercase;
  margin: 0 0 14px;
}
.modal__text {
  font-size: 15px;
  line-height: 30px;
  text-align: center;
  margin: 0 0 21.5px;
}
.modal__button-wrapper {
  display: flex;
  column-gap: 7px;
  margin-bottom: 31px;
}
.modal__button-size-select {
  display: block;
  flex-grow: 1;
  padding: 18px 0;
  font-weight: bold;
  font-size: 17px;
  line-height: 16px;
  text-transform: uppercase;
  color: #444444;
  border: 2px solid #44444410;
  background-color: #ffffff;
  box-sizing: border-box;
}
.modal__button-size-select:hover {
  border-color: #44444430;
}
.modal__button-size-select:active {
  border-color: #62d1ba;
}
.modal__button-add {
  display: block;
  width: 100%;
  padding: 18px 0;
  font-weight: bold;
  font-size: 17px;
  line-height: 16px;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #63d1bb;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .modal {
    padding: 37.5px 105px 46px;
    max-width: 566px;
  }
  .modal__title {
    font-size: 30px;
    line-height: 57px;
  }
  .modal__text {
    font-size: 17px;
    margin-bottom: 19.5px;
  }
}
